.sci {
  display: flex;
  gap: 50px;
  @include mobile {
    gap: 40px;
  }
  // .sci__item

  &__item {
  }

  // .sci__link

  &__link {
    position: relative;
    display: inline-flex;
    width: 55px;
    height: 55px;
    transition: transform 0.3s ease-in-out;
    &:hover{
      transform: translateY(-5px);
    }
  }
  &__image {
    display: inline-block;
    width: 100%;
    height: 100%;
  }
}
