.map {
  margin: 100px 0;
  &__title {

  }
  &__descr {
    font-size: 24px;
    line-height: 140%;
    color: #444649;
    text-align: center;
    margin: 0 0 70px 0;
  }
  &__wrapper {
    position: relative;
    width: 100%;
    height: 559px;
    max-width: 1920px;
    margin: 0 auto;
    box-shadow: 0 0 2em 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
    padding: 0;
    z-index: 10;
    background-color: #fff;
    background-image: url("../img/map.png");
    background-position: center center;
    background-size: cover;
    overflow: hidden;
    @include small-tablet {
      height: 331px;
    }
  }
  &__box {
    position: relative;
    z-index: 10;
    width: 100%;
    height: 548px;
    background-color: transparent;
    cursor: pointer;
    @include small-tablet {
      height: 331px;
    }
  }
}
[class*="copyrights-pane"] {
  display: none !important;
}

