/* Индикатор загрузки, который показывается до загрузки карты */
.loader {
  position: absolute;
  z-index: 15;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  color: #000000;
  transition: opacity 0.7s ease, visibility 0.7s ease;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.55);
}
.loader.is-active {
  top: 0;
  opacity: 1;
  visibility: visible;
}
.loader-default:after {
  position: absolute;
  top: calc(50% - 24px);
  left: calc(50% - 24px);
  width: 48px;
  height: 48px;
  content: "";
  animation: rotation 1s linear infinite;
  border: solid 8px #ffffff;
  border-left-color: transparent;
  border-radius: 50%;
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}
