.footer {
  padding: 100px 0;
  // .footer__container

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
    @media (max-width: (1100px)) {
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: 20px;
    }
    @include small-tablet {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  // .footer__descr

  &__descr {
  }

  // .footer__title

  &__title {
    font-family: "Kotto One", sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 140%;
    color: #869ac7;
    text-transform: uppercase;
    margin: 0;
    white-space: nowrap;
  }

  // .footer__text

  &__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 114%;
    color: #869ac7;
    margin: 0;
  }

  &__list {
  }
}


